.button-clear {
  height: 80px;
  font-size: 1.6em;
  display: flex;
  flex: 1;
  background-color: #ac0246;
  margin-top: 8px;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: white;
  border:2px solid white;
  border-radius: 20px;
  cursor: pointer;  
  user-select: none;

}
.button-clear:hover{
  background-color: #ebb000;

}